"use client";
import React, { useEffect, useState } from "react";
import { navigate } from "gatsby";
import {
  FiArrowLeft,
  FiDownload,
  FiStar,
  FiMapPin,
  FiAward,
  FiMail,
  FiPhone,
  FiGlobe,
  FiFileText,
  FiExternalLink,
  FiYoutube,
  FiFacebook,
  FiMessageCircle,
} from "react-icons/fi";
import { Honoree, honoreesData } from "../../componets/Honorees/data";
import { findHonoreeBySlug } from "../../componets/Honorees/slugUtils";
import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content";
import Header from "../../componets/header/Header";
import Footer from "../../componets/footer/Footer";
import "../../componets/styles/profile.css";
import HideSection from "../shared/hide-section/HideSection";

import { useStaticConfig } from "../../hooks/useStaticConfig";


const { 
  HIDE_CONTACT_ON_PUBLIC_PROFILE,
  ROADMAP_HIDE_RELATED_KEYWORDS, 
  ROADMAP_HIDE_SOCIALS_ON_PROFILE, 
} = useStaticConfig();




const BreadCrumbSubheader = ({
  breadcrumbs,
  content,
  preview,
}: {
  breadcrumbs?: any[];
  content?: any;
  preview?: boolean;
}) => {
  if (preview) return <></>;
  return (
    <>
      <div className="container">
        <nav className="breadcrumb">
          {breadcrumbs?.map((crumb, index) => (
            <React.Fragment key={index}>
              {crumb.href ? (
                <button
                  onClick={() => navigate(crumb.href)}
                  className="breadcrumb-link"
                >
                  {crumb.label}
                </button>
              ) : (
                <span className="breadcrumb-current">{crumb.label}</span>
              )}
              {index < breadcrumbs.length - 1 && (
                <span className="breadcrumb-separator"></span>
              )}
            </React.Fragment>
          ))}
        </nav>

        <button onClick={() => navigate("/honorees")} className="back-link">
          <FiArrowLeft className="back-icon" />{" "}
          {content.honoreeProfile.section.backToHonorees}
        </button>
      </div>
    </>
  );
};

export default function HonoreeProfile({
  honoreeData,
  preview,
}: {
  honoreeData?: any;
  preview?: boolean;
}) {
  const { lang } = useLanguage();
  const content = getContent(lang);
  const [honoree, setHonoree] = useState<Honoree | null>(null);
  const [loading, setLoading] = useState(true);
  const previewProps: any = { preview };

  const [profileData, setProfileData] = useState({
    stats: [
      { label: content.honoreeProfile.section.stats.patents, value: "127" },
      { label: content.honoreeProfile.section.stats.projects, value: "45" },
      { label: content.honoreeProfile.section.stats.initiatives, value: "15" },
      { label: content.honoreeProfile.section.stats.students, value: "200+" },
    ],
    certificate: {
      code: "Wt-KL-2024-091",
      date: "14/11/2024",
      status: content.honoreeProfile.section.certificate.status,
    },
    achievements: [],
    press: [],
    keywords: [],
    contact: {
      email: "",
      phone: "",
      address: "",
    },
  });

  //  Load base honoree (from slug or provided data)
  useEffect(() => {
    const h: any = honoreeData ?? honoreesData[0];
    if (h) {
      setHonoree(h);
      setLoading(false);
    } else {
      const urlParams = new URLSearchParams(window.location.search);
      const pathSlug =
        window.location.pathname.split("/").pop() ||
        urlParams.get("slug") ||
        "";
      const foundHonoree = findHonoreeBySlug(pathSlug);
      setHonoree(foundHonoree || null);
      setLoading(false);
    }
  }, [honoreeData]);

  //  Sync dynamic preview data when honoreeData changes
  useEffect(() => {
    if (!honoreeData) return;

    setProfileData({
      stats:
        honoreeData.statistics?.map((s: any) => ({
          label: s.label || "",
          value: s.value || "",
        })) ||
        [],
      certificate:
        honoreeData.certificates?.[0]
          ? {
              code: honoreeData.certificates[0].code || "",
              date: honoreeData.certificates[0].issue_date || "",
              status: content.honoreeProfile.section.certificate.status,
            }
          : {
              code: "",
              date: "",
              status: content.honoreeProfile.section.certificate.status,
            },
      achievements:
        honoreeData.achievements?.map((a: any) => ({
          year: a.year,
          title: a.title,
          description: a.description,
          icon: <FiAward className="icon" />,
        })) || [],
      press:
        honoreeData.press?.map((p: any) => ({
          date: p.date,
          title: p.title,
          description: p.summary || p.description,
          icon: <FiFileText className="icon" />,
        })) || [],
      keywords: honoreeData.keywords || [],
      contact: honoreeData.contact || {
        email: "",
        phone: "",
        address: "",
      },
    });
  }, [honoreeData, content]);

  if (loading) {
    return (
      <div>
        <Header {...previewProps} />
        <div className="loading-container">
          <div className="loading-text">
            {content.honoreeProfile.section.loading}
          </div>
        </div>
        <Footer {...previewProps} />
      </div>
    );
  }

  if (!honoree) {
    return (
      <div>
        <Header {...previewProps} />
        <div className="not-found-container">
          <div className="not-found-content">
            <h1 className="not-found-title">
              {content.honoreeProfile.section.notFound.title}
            </h1>
            <button onClick={() => navigate("/honorees")} className="back-button">
              {content.honoreeProfile.section.notFound.backButton}
            </button>
          </div>
        </div>
        <Footer {...previewProps} />
      </div>
    );
  }

  const breadcrumbs = [
    {
      label: content.pages.globals.section.headerContent.navLinks[0].text,
      href: "/",
    },
    {
      label: content.pages.globals.section.headerContent.navLinks[3].text,
      href: "/honorees",
    },
    { label: honoree.name, href: "" },
  ];

  return (
    <div className="honoree-profile">
      <Header {...previewProps} />
      <BreadCrumbSubheader
        breadcrumbs={breadcrumbs}
        content={content}
        preview={preview}
      />

      {/* HERO */}
      <div className="hero-wrapper">
        <div className="hero-card">
          <div className="hero-header">
            <div className="hero-content">
              <div className="hero-info">
                <h1 className="hero-name">{honoree.name}</h1>
                <div className="hero-meta">
                  <div className="category-badge">{honoree.category}</div>
                  <div className="star-rating">
                    {[...Array(5)].map((_, i) => (
                      <FiStar key={i} className="star" />
                    ))}
                  </div>
                </div>
                <p className="hero-description">
                  {honoree.fieldOfEstablishment}
                </p>
              </div>
            </div>

            {/* Stats Grid */}
            <div className="hero-stats">
              {profileData.stats.map((s, i) => (
                <div className="stat-item" key={i}>
                  <div className="stat-number">{s.value}</div>
                  <div className="stat-label">{s.label}</div>
                </div>
              ))}
            </div>
          </div>

          {/* Certificate Section */}
          <div className="certificate-section">
            <div className="certificate-content">
              <div className="certificate-info">
                <div className="certificate-icon">
                  <FiAward className="award-icon" />
                </div>
                <div>
                  <h3 className="certificate-title">
                    {content.honoreeProfile.section.certificate.title}
                  </h3>
                  <p className="certificate-details">
                    {content.honoreeProfile.section.certificate.code}{" "}
                    <span className="certificate-code">
                      {profileData.certificate.code}
                    </span>{" "}
                    {" "}
                    {content.honoreeProfile.section.certificate.date}{" "}
                    {profileData.certificate.date}
                  </p>
                </div>
              </div>
              <div className="status-badge">
                <div className="status-dot"></div>
                <span className="status-text">
                  {profileData.certificate.status}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* CONTENT */}
      <div className="hero-wrapper">
        <div className="">
          <div className="main-content">
            {/* Achievements */}
            <div className="content-card">
              <div className="card-header">
                <h2 className="card-title">
                  <div className="title-icon">
                    <FiAward className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.achievements}
                </h2>
              </div>
              <div className="card-body">
                {profileData.achievements.map((a, i) => (
                  <div className="timeline-item" key={i}>
                    <div className="timeline-content">
                      <div className="timeline-icon">{a.icon}</div>
                      <div className="timeline-details">
                        <div className="timeline-header">
                          <h3 className="timeline-title">{a.title}</h3>
                          <span className="timeline-year">{a.year}</span>
                        </div>
                        <p className="timeline-description">{a.description}</p>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Press */}
            <br />
            <div className="content-card">
              <div className="card-header press">
                <h2 className="card-title">
                  <div className="title-icon press">
                    <FiFileText className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.news}
                </h2>
              </div>
              <div className="card-body">
                {profileData.press.map((p, i) => (
                  <article className="news-article" key={i}>
                    <div className="article-content">
                      <div className="article-icon">{p.icon}</div>
                      <div className="article-details">
                        <div className="article-meta">
                          <span className="article-date">{p.date}</span>
                          <FiExternalLink className="external-icon" />
                        </div>
                        <h3 className="article-title">{p.title}</h3>
                        <p className="article-description">{p.description}</p>
                      </div>
                    </div>
                  </article>
                ))}
              </div>
            </div>

            {/* Keywords */}
            <HideSection hide={ROADMAP_HIDE_RELATED_KEYWORDS}>
              <br />
              <div className="content-card">
                <div className="card-header keywords">
                  <h2 className="card-title">
                    <div className="title-icon keywords">
                      <FiStar className="icon" />
                    </div>
                    {content.honoreeProfile.section.sections.keywords}
                  </h2>
                </div>
                <div className="card-body">
                  <div className="keywords-container">
                    {profileData.keywords.map((tag, index) => (
                      <span key={index} className="keyword-tag">
                        #{tag}
                      </span>
                    ))}
                  </div>
                </div>
              </div>
            </HideSection>
          </div>

          {/* Sidebar */}
          <div className="sidebar">
            {/* Contact Info */}
            <HideSection hide={HIDE_CONTACT_ON_PUBLIC_PROFILE}>
              <br />
              <div className="sidebar-card">
                <div className="card-header contact">
                  <h3 className="card-title">
                    <div className="title-icon contact">
                      <FiMail className="icon" />
                    </div>
                    {content.honoreeProfile.section.sections.contact}
                  </h3>
                </div>
                <div className="card-body">
                  <div className="contact-item">
                    <div className="contact-icon email">
                      <FiMail className="icon" />
                    </div>
                    <div className="contact-details">
                      <div className="contact-label">
                        {content.honoreeProfile.section.contact.email}
                      </div>
                      <div className="contact-value">
                        {profileData.contact.email}
                      </div>
                    </div>
                  </div>
                  <div className="contact-item">
                    <div className="contact-icon phone">
                      <FiPhone className="icon" />
                    </div>
                    <div className="contact-details">
                      <div className="contact-label">
                        {content.honoreeProfile.section.contact.phone}
                      </div>
                      <div className="contact-value">
                        {profileData.contact.phone}
                      </div>
                    </div>
                  </div>
                  <div className="contact-item">
                    <div className="contact-icon address">
                      <FiMapPin className="icon" />
                    </div>
                    <div className="contact-details">
                      <div className="contact-label">
                        {content.honoreeProfile.section.contact.address}
                      </div>
                      <div className="contact-value">
                        {profileData.contact.address}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </HideSection>

            {/* QR Card */}
            <br />
            <div className="sidebar-card">
              <div className="card-header">
                <h3 className="card-title">
                  <div className="title-icon">
                    <FiDownload className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.qrCode}
                </h3>
              </div>
              <div className="card-body">
                <div className="qr-code">
                  <img
                    src="data:image/png;base64,"
                    alt="QR Code"
                  />
                </div>
                <p className="qr-description">
                  {content.honoreeProfile.section.qr.description}
                </p>
                <button className="download-button">
                  <FiDownload className="button-icon" />
                  {content.honoreeProfile.section.qr.download}
                </button>
              </div>
            </div>

            {/* Social Buttons */}
            <HideSection hide={ROADMAP_HIDE_SOCIALS_ON_PROFILE}>
              <div className="sidebar-card">
                <div className="card-header social">
                  <h3 className="card-title">
                    <div className="title-icon social">
                      <FiMessageCircle className="icon" />
                    </div>
                    {content.honoreeProfile.section.sections.social}
                  </h3>
                </div>
                <div className="card-body">
                  <div className="social-grid">
                    <button className="social-button youtube">
                      <FiYoutube className="social-icon" />
                    </button>
                    <button className="social-button facebook">
                      <FiFacebook className="social-icon" />
                    </button>
                    <button className="social-button message">
                      <FiMessageCircle className="social-icon" />
                    </button>
                    <button className="social-button phone">
                      <FiPhone className="social-icon" />
                    </button>
                  </div>
                </div>
              </div>
            </HideSection>
          </div>
        </div>
      </div>

      <br />
      <Footer {...previewProps} />
    </div>
  );
}
