import React, { useEffect, useState, useMemo } from "react";
import HonoreePreviewCard from "./HonoreePreviewCard";
import HideSection from "../shared/hide-section/HideSection";


import StatisticsCard from "./HonoreeStatisticsCard";
import AchievementsCard from "./HonoreeAchievementsCard";
import PressNewsCard from "./HonoreePressNewsCard";
import CertificatesCard from "./HonoreeCertificatesCard";
import MainInfoCard from "./HonoreeProfileData";
import HonoreeProfile from "../honorees/HonoreeProfile";
import { supabase } from "../../database/supabaseClient";

import {
  FiArrowLeft,
  FiDownload,
  FiStar,
  FiMapPin,
  FiCalendar,
  FiAward,
  FiMail,
  FiPhone,
  FiGlobe,
  FiFileText,
  FiExternalLink,
} from "react-icons/fi";

import { ArrowLeft, ArrowRight, CheckCircle2 } from "lucide-react";
import { qstyle } from "./mini-studio/public-profile-editor";

export const ICONS = {
  FiArrowLeft,
  FiDownload,
  FiStar,
  FiMapPin,
  FiCalendar,
  FiAward,
  FiMail,
  FiPhone,
  FiGlobe,
  FiFileText,
  FiExternalLink,
};

/* ------------------------------
   Status Card
------------------------------ */
const StatusCard = ({ statusSteps, currentStep, handleNext, handlePrev, currentLabel }: any) => (
  <div
    className="cms-card"
    style={{
      marginTop: "20px",
      border: "1px solid var(--border)",
      borderRadius: "10px",
      padding: "20px",
    }}
  >
    <div
      className="status-header"
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        marginBottom: "10px",
      }}
    >
      <h3 style={{ margin: 0, fontSize: "15px", fontWeight: 600 }}>Status Progress</h3>
      <span style={{ fontSize: "13px", color: "var(--muted)" }}>
        Current: <b style={{ color: "var(--accent)" }}>{currentLabel}</b>
      </span>
    </div>

    <div
      className="status-steps"
      style={{
        display: "flex",
        justifyContent: "space-between",
        margin: "15px 0 10px",
      }}
    >
      {statusSteps.map((s: any) => {
        const done = s.step < currentStep;
        const active = s.step === currentStep;
        return (
          <div key={s.step} style={{ textAlign: "center", flex: 1 }}>
            <div
              style={{
                width: "28px",
                height: "28px",
                borderRadius: "50%",
                border: `2px solid ${
                  done ? "var(--accent)" : active ? "var(--accent)" : "var(--border)"
                }`,
                background: done ? "var(--accent)" : "transparent",
                color: done ? "#fff" : active ? "var(--accent)" : "var(--muted)",
                margin: "0 auto",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                fontSize: "12px",
              }}
            >
              {done ? <CheckCircle2 size={14} /> : s.step}
            </div>
            <div
              style={{
                fontSize: "11px",
                marginTop: "5px",
                color: active ? "var(--accent)" : "var(--muted)",
                fontWeight: active ? 500 : 400,
              }}
            >
              {s.label}
            </div>
          </div>
        );
      })}
    </div>

    <div
      className="status-controls"
      style={{
        display: "flex",
        justifyContent: "space-between",
        borderTop: "1px solid var(--border)",
        paddingTop: "15px",
      }}
    >
      <button onClick={handlePrev} disabled={currentStep === 1} style={{ opacity: currentStep === 1 ? 0.5 : 1 }}>
        <ArrowLeft size={14} /> Back
      </button>
      <button
        onClick={handleNext}
        disabled={currentStep === statusSteps.length}
        className="primary-btn"
        style={{
          opacity: currentStep === statusSteps.length ? 0.6 : 1,
          cursor: currentStep === statusSteps.length ? "not-allowed" : "pointer",
        }}
      >
        Next <ArrowRight size={14} />
      </button>
    </div>
  </div>
);

export default function HonoreeAdvancedProfileEditor({ id, data }: any) {
  const [previewMode, setPreviewMode] = useState<"edit" | "social" | "profile">("edit");
  const [honoreePreviewData, setHonoreePreviewData] = useState<any>(null);

  const [form, setForm] = useState({
    name: "",
    type: "",
    category: "",
    field: "",
    location: "",
    image: "",
    status: "",
    title: "",
    email: "",
    phone: "",
    address: "",
    membership_level: "",
    dob: "",
    personal_goals: "",
    future_vision: "",
  });

  console.log(form)

  const [stats, setStats] = useState<{ label: string; value: string }[]>([]);
  const [achievements, setAchievements] = useState<
    { year: string; title: string; description: string; icon?: keyof typeof ICONS }[]
  >([]);
  const [certificates, setCertificates] = useState<
    { code: string; title: string; issue_date: string; description?: string }[]
  >([]);
  const [press, setPress] = useState<
    { date: string; title: string; summary: string; link?: string }[]
  >([]);
  const [keywords, setKeywords] = useState<string[]>([]);
  const [keywordInput, setKeywordInput] = useState("");

  //  Status progress setup
  const statusSteps = useMemo(
    () => [
      { step: 1, label: "Profile Submitted" },
      { step: 2, label: "Under Review" },
      { step: 3, label: "Evaluation" },
      { step: 4, label: "Approved" },
    ],
    []
  );
  const [currentStep, setCurrentStep] = useState(1);
  const currentLabel = statusSteps.find((s) => s.step === currentStep)?.label;
  const handleNext = () => currentStep < statusSteps.length && setCurrentStep(currentStep + 1);
  const handlePrev = () => currentStep > 1 && setCurrentStep(currentStep - 1);

  // load incoming data
  useEffect(() => {
    if (!id || !data) return;

    setForm({
      name: data.name || "",
      type:
        ["Individual", "Organization", "Product/Service", "Cultural Heritage"].includes(data.type)
          ? data.type
          : "",
      category: data.category || "",
      field: data.field || "",
      location: data.location || "",
      image: data.image_url || "",
      status: ["Pending", "Active", "Inactive"].includes(data.status) ? data.status : "",
      title: data.title || "",
      email: data.email || "",
      phone: data.phone || "",
      address: data.address || "",
      membership_level: data.membership_level || "",
      dob: data.dob || "",
      personal_goals: data.personal_goals || "",
      future_vision: data.future_vision || "",
    });

    setStats(data.statistics || []);
    setAchievements(data.achievements || []);
    setPress(data.press || []);
    setCertificates(data.certificates || []);
    setKeywords(data.keywords || []);
    if (data.application_process) setCurrentStep(data.application_process);
  }, [id, data]);

  const updateField = (key: string, value: any) => setForm((p) => ({ ...p, [key]: value }));

  const handleKeyword = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter" && keywordInput.trim()) {
      e.preventDefault();
      if (!keywords.includes(keywordInput.trim())) {
        setKeywords((k) => [...k, keywordInput.trim()]);
      }
      setKeywordInput("");
    }
  };

const handleSave = async () => {
  const user_id = id;
  if (!user_id) {
    alert(" Missing user ID  cannot save profile.");
    return;
  }

  console.log(" STARTING SAVE for user_id:", user_id);

  try {
    // === Prepare data ===
    const achievementsForDB = achievements.map(({ year, title, description }) => ({
      year,
      title,
      description,
    }));
    const statisticsForDB = stats;
    const pressForDB = press;

    let certificate_info: string | null = null;
    if (certificates && certificates.length > 0) {
      const first = certificates[0];
      certificate_info = JSON.stringify({
        code: first.code || "",
        title: first.title || "",
        date: first.issue_date || "",
      });
    }

    console.log(" Prepared Data:", {
      achievementsForDB,
      statisticsForDB,
      pressForDB,
      certificate_info,
      currentStep,
      form,
    });

    // === 1 user_member_v2  public-facing profile ===
    const memberUpdate: any = {
      full_name: form.name || null,
      title: form.title || null,
      field_of_expertise: form.field || null,
      avatar_url: form.image || null,
      hometown: form.location || null,
      updated_at: new Date().toISOString(),
    };

    console.log(" Updating user_member_v2 ", memberUpdate);
    const { error: memberError } = await supabase
      .from("user_member_v2")
      .update(memberUpdate)
      .eq("id", user_id);
    if (memberError) throw new Error("user_member_v2: " + memberError.message);

    // === 2 user_private_v2  personal/private info ===
    const privateUpdate = {
      email: form.email || null,
      phone: form.phone || null,
      address: form.address || null,
      dob: form.dob || null,
      personal_goals: form.personal_goals || null,
      future_vision: form.future_vision || null,
      updated_at: new Date().toISOString(),
    };

    console.log(" Updating user_private_v2 ", privateUpdate);
    const { error: privateError } = await supabase
      .from("user_private_v2")
      .update(privateUpdate)
      .eq("id", user_id);
    if (privateError) throw new Error("user_private_v2: " + privateError.message);

    // === 3 user_credentials  membership and status ===
    const credentialsUpdate = {
      membership_level: form.membership_level || null,
      profile_status: form.status || null,
      statistics: statisticsForDB,
      achievements: achievementsForDB,
      press: pressForDB,
      certificate_info,
      application_process: currentStep,
      updated_at: new Date().toISOString(),
    };

    console.log(" Updating user_credentials ", credentialsUpdate);
    const { error: credError } = await supabase
      .from("user_credentials")
      .update(credentialsUpdate)
      .eq("id", user_id);
    if (credError) throw new Error("user_credentials: " + credError.message);

    console.log(" ALL UPDATES SUCCESSFUL!");
    alert(" Profile and progress saved successfully!");
  } catch (err: any) {
    console.error(" ERROR DURING SAVE ", err);
    alert(" Failed to save profile: " + (err?.message || "Unknown error"));
  }
};




  const buildHonoreeData = () => ({
    name: form.name || "Preview Honoree",
    category: form.category || form.field || "Cultural Elites",
    fieldOfEstablishment: form.field || "",
    image: form.image,
    statistics: stats,
    achievements,
    press,
    certificates,
    keywords,
    contact: {
      email: form.email || "",
      phone: form.phone || "",
      address: form.address || "",
    },
  });

  return (
    <div className="cms-wrapper">
      <style>{qstyle}</style>

      {/* Header actions */}
      <div
        className="cms-card"
        style={{
          display: "flex",
          justifyContent: "space-between",
          flexWrap: "wrap",
          alignItems: "center",
        }}
      >
        <button className="primary-btn" onClick={handleSave}>
           Save Profile
        </button>

        <div style={{ display: "flex", gap: "10px" }}>
          <button
            onClick={() =>
              setPreviewMode(previewMode === "social" ? "edit" : "social")
            }
          >
             {previewMode === "social" ? "Back to Edit" : "Preview Social Card"}
          </button>

          <button
            onClick={() => {
              if (previewMode === "profile") {
                setPreviewMode("edit");
                setHonoreePreviewData(null);
              } else {
                const snapshot = buildHonoreeData();
                setHonoreePreviewData(snapshot);
                setPreviewMode("profile");
              }
            }}
          >
             {previewMode === "profile" ? "Back to Edit" : "Preview Profile"}
          </button>
        </div>
      </div>

      {/*  Inserted Status Progress Tracker */}
      <StatusCard
        statusSteps={statusSteps}
        currentStep={currentStep}
        handleNext={handleNext}
        handlePrev={handlePrev}
        currentLabel={currentLabel}
      />

      {/* Conditional preview */}
      {previewMode === "social" ? (
        <HonoreePreviewCard
          data={{ form, stats, achievements, certificates, press, keywords }}
        />
      ) : previewMode === "profile" ? (
        <HonoreeProfile preview honoreeData={honoreePreviewData} />
      ) : (
        <>
          <div className="section-divider" />
          <MainInfoCard form={form} onChange={updateField} />
          <div className="section-divider" />
          <StatisticsCard
            stats={stats}
            onAdd={() => setStats((s) => [...s, { label: "", value: "" }])}
            onRemove={(i) => setStats((s) => s.filter((_, x) => x !== i))}
            onChange={(i, key, val) => {
              const arr = [...stats];
              (arr[i] as any)[key] = val;
              setStats(arr);
            }}
          />
          <div className="section-divider" />
          <AchievementsCard
            achievements={achievements}
            icons={ICONS}
            onAdd={() =>
              setAchievements((a) => [
                ...a,
                { year: "", title: "", description: "", icon: "FiStar" },
              ])
            }
            onRemove={(i) => setAchievements((a) => a.filter((_, x) => x !== i))}
            onChange={(i, key, val) => {
              const arr = [...achievements];
              (arr[i] as any)[key] = val;
              setAchievements(arr);
            }}
          />
          <div className="section-divider" />
          <CertificatesCard
            certificates={certificates}
            onAdd={() => setCertificates((c) => [...c, { code: "", title: "", issue_date: "" }])}
            onRemove={(i) => setCertificates((c) => c.filter((_, x) => x !== i))}
            onChange={(i, key, val) => {
              const arr = [...certificates];
              (arr[i] as any)[key] = val;
              setCertificates(arr);
            }}
          />
          <div className="section-divider" />
          <PressNewsCard
            press={press}
            onAdd={() => setPress((p) => [...p, { date: "", title: "", summary: "" }])}
            onRemove={(i) => setPress((p) => p.filter((_, x) => x !== i))}
            onChange={(i, key, val) => {
              const arr = [...press];
              (arr[i] as any)[key] = val;
              setPress(arr);
            }}
          />
        </>
      )}
    </div>
  );
}
