import React from "react";

type Certificate = {
  code: string;
  title: string;
  issue_date: string;
  description: string;
};

export default function CertificatesCard({
  certificates,
  onAdd,
  onRemove,
  onChange,
}: {
  certificates: Certificate[];
  onAdd: () => void;
  onRemove: (index: number) => void;
  onChange: (index: number, key: keyof Certificate, value: string) => void;
}) {
  return (
    <>
      {/* Header */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginBottom: "14px",
          marginTop: "20px",
        }}
      >
        <h3 style={{ fontSize: "16px", fontWeight: 600 }}>Certificates</h3>
        <button
          onClick={onAdd}
          style={{
            background: "var(--accent)",
            color: "#072c21",
            fontWeight: 500,
            borderRadius: "6px",
            padding: "6px 12px",
            cursor: "pointer",
          }}
        >
          + Add Certificate
        </button>
      </div>

      {/* Cards */}
      <div className="cert-list">
        {certificates.map((c, i) => (
          <div key={i} className="cert-card">
            <div className="row">
              <input
                placeholder="Certificate Code"
                value={c.code}
                onChange={(e) => onChange(i, "code", e.target.value)}
              />
              <input
                placeholder="Title"
                value={c.title}
                onChange={(e) => onChange(i, "title", e.target.value)}
              />
              <input
                type="date"
                style={{ width: "150px" }}
                value={c.issue_date}
                onChange={(e) => onChange(i, "issue_date", e.target.value)}
              />
              <button className="trash-btn" onClick={() => onRemove(i)}>
                
              </button>
            </div>

            <textarea
              placeholder="Description or notes"
              value={c.description}
              onChange={(e) => onChange(i, "description", e.target.value)}
            />
          </div>
        ))}
      </div>

      <style>{`
        .cert-list {
          display: flex;
          flex-direction: column;
          gap: 18px;
        }

        .cert-card {
          background: var(--panel);
          border: 1px solid var(--border);
          border-radius: 10px;
          padding: 16px;
          box-shadow: 0 4px 10px rgba(0,0,0,0.08);
        }

        .cert-card .row {
          display: flex;
          gap: 10px;
          align-items: center;
          margin-bottom: 10px;
        }

        .cert-card input,
        .cert-card textarea {
          background: var(--panel);
          border: 1px solid var(--border);
          border-radius: 6px;
          color: var(--text);
          font-size: 13px;
          padding: 8px 10px;
          width: 100%;
        }

        .cert-card textarea {
          resize: vertical;
          min-height: 60px;
        }

        .trash-btn {
          border: 1px solid var(--border);
          border-radius: 6px;
          padding: 6px 10px;
          height: 34px;
          cursor: pointer;
          background: var(--panel);
        }

        .trash-btn:hover {
          background: var(--accent-bg);
          color: var(--accent);
        }
      `}</style>
    </>
  );
}
