import React, { useEffect, useMemo, useState } from "react";
import HonoreePublicProfileEditor from "../../../../componets/cms/HonoreePublicProfileEditor";
import CmsHeader from "../../../../componets/cms/CmsHeader";
import { useLocation } from "@reach/router";
import { supabase } from "../../../../database/supabaseClient";
import { qstyle } from "../../../../componets/cms/honoree-quickstyle";


export default function HonoreePage() {
  const [theme, setTheme] = useState<"light" | "dark">("light");
  const [honoreeData, setHonoreeData] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [summaries, setSummaries] = useState<Record<string, string>>({});

  const location = useLocation();

  // Extract user ID from URL path (.../honoree/:id/)
  const userId = useMemo(() => {
    const parts = (location?.pathname || "").replace(/\/+$/, "").split("/");
    return parts[parts.length - 2] || undefined;
  }, [location?.pathname]);

  //  Known category values
  const CATEGORY_OPTIONS = [
    "Cultural Ambassador",
    "Cultural Enthusiast",
    "Cultural Curator",
    "Artist",
    "Writer",
    "Performer",
  ];

  useEffect(() => {
    const fetchHonoree = async () => {
      if (!userId) return;
      setLoading(true);

      try {
        console.log(" Fetching honoree data for:", userId);

        // Include credentials table too
        const tables = ["user_member_v2", "user_private_v2", "user_credentials"];
        const results: Record<string, any[]> = {};
        const newSummaries: Record<string, string> = {};

        for (const table of tables) {
          const { data, error } = await supabase
            .from(table)
            .select("*")
            .eq("id", userId);
          console.log(` ${table} `, data || [], error ? " " + error.message : "ok");
          results[table] = data || [];
          newSummaries[table] = data?.length
            ? ` ${data.length} rows visible`
            : error
            ? ` ${error.message}`
            : " No rows (RLS restricted)";
        }

        setSummaries(newSummaries);

        const m = results["user_member_v2"]?.[0] || {};
        const p = results["user_private_v2"]?.[0] || {};
        const c = results["user_credentials"]?.[0] || {};

        //  Determine category based on title
        const cleanTitle = (m.title || "").trim();
        const matchedCategory = CATEGORY_OPTIONS.find(
          (c) => c.toLowerCase() === cleanTitle.toLowerCase()
        );
        const derivedCategory = matchedCategory || undefined;

        //  Normalize certificate_info  handles string or object
        let cert = c.certificate_info || null;
        let certificateArray: any[] = [];

        try {
          if (typeof cert === "string") {
            cert = JSON.parse(cert);
          }

          if (Array.isArray(cert)) {
            certificateArray = cert;
          } else if (cert && typeof cert === "object") {
            certificateArray = [
              {
                code: cert.code || "",
                title: cert.title || "",
                issue_date: cert.issue_date || cert.date || "",
                description: cert.description || "",
              },
            ];
          }
        } catch (err) {
          console.warn(" Failed to parse certificate_info JSON:", err);
        }

        //  Merge into one unified object
        const mapped = {
          id: userId,
          name: m.full_name || "",
          //  category type like "Individual" or "Organization"
          type: c.type || "Individual", 

          //  membership level like "Member", "Admin", "Honorary Member"
          membership_level: c.membership_level || "", 

          category: derivedCategory,
          field: m.field_of_expertise || "",
          location: m.hometown || "",
          image_url: m.avatar_url || "",
          status: c.profile_status || "Pending",
          statistics: c.statistics || [],
          achievements: c.achievements || [],
          press: c.press || [],
          certificates: certificateArray,
          application_process: c.application_process || 1,
          email: p.email || "",
          phone: p.phone || "",
          address: p.address || "",
          dob: p.dob || "",
          personal_goals: p.personal_goals || "",
          future_vision: p.future_vision || "",
        };


        console.log(" Final mapped honoree data ", mapped);
        setHonoreeData(mapped);
      } catch (err: any) {
        console.error(" Failed to load honoree:", err.message);
        setSummaries({ error: ` ${err.message}` });
      } finally {
        setLoading(false);
      }
    };

    fetchHonoree();
  }, [userId]);

  return (
    <div className={`app ${theme}`}>
      <style>{qstyle}</style>
      <CmsHeader title="Tinh Hoa Vit CMS" onThemeChange={setTheme} />

      <main>
        {loading ? (
          <p>Loading honoree data...</p>
        ) : honoreeData ? (
          <>
            {/* <div
              style={{
                background: "#f5f5f5",
                padding: "10px",
                marginBottom: "10px",
                borderRadius: "6px",
              }}
            >
              <h4>Debug Summary</h4>
              <ul>
                {Object.entries(summaries).map(([tbl, msg]) => (
                  <li key={tbl}>
                    <strong>{tbl}:</strong> {msg}
                  </li>
                ))}
              </ul>
            </div> */}

            {/*  Force remount when data changes */}
            <HonoreePublicProfileEditor
              key={honoreeData?.id || userId}
              id={userId}
              data={honoreeData}
            />


          
          </>
        ) : (
          <p>No honoree found for ID: {userId}</p>
        )}
      </main>
    </div>
  );
}
