import React from "react";

type Stat = { label: string; value: string };

export default function StatisticsCard({
  stats,
  onAdd,
  onRemove,
  onChange,
}: {
  stats: Stat[];
  onAdd: () => void;
  onRemove: (index: number) => void;
  onChange: (index: number, key: keyof Stat, value: string) => void;
}) {
  return (
    <>
      {/* Header section (on gray background) */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginBottom: "14px",
          marginTop: "20px",
        }}
      >
        <h3 style={{ fontSize: "16px", fontWeight: 600 }}>Statistics</h3>
        <button
          onClick={onAdd}
          className="primary-btn"
          style={{
            background: "var(--accent)",
            color: "#072c21",
            fontWeight: 500,
            borderRadius: "6px",
            padding: "6px 12px",
          }}
        >
          + Add Statistic
        </button>
      </div>

      {/* Stats grid */}
      <div className="stats-grid">
        {stats.map((s, i) => (
          <div key={i} className="stat-card">
            <input
              placeholder="Title"
              value={s.label}
              onChange={(e) => onChange(i, "label", e.target.value)}
            />
            <input
              placeholder="Value"
              value={s.value}
              onChange={(e) => onChange(i, "value", e.target.value)}
            />
            <button className="trash-btn" onClick={() => onRemove(i)}>
              
            </button>
          </div>
        ))}
      </div>

      <style>{`
        .stats-grid {
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
          gap: 16px;
          margin-bottom: 20px;
        }

        .stat-card {
          display: flex;
          align-items: center;
          gap: 10px;
          background: var(--panel);
          border: 1px solid var(--border);
          border-radius: 10px;
          padding: 12px 14px;
          box-shadow: 0 4px 10px rgba(0,0,0,0.08);
        }

        .stat-card input {
          flex: 1;
          background: var(--panel);
          border: 1px solid var(--border);
          border-radius: 6px;
          color: var(--text);
          font-size: 13px;
          padding: 8px 10px;
          min-width: 0;
        }

        .trash-btn {
          border: 1px solid var(--border);
          border-radius: 6px;
          padding: 6px 10px;
          height: 34px;
          cursor: pointer;
          background: var(--panel);
          flex-shrink: 0;
        }

        .trash-btn:hover {
          background: var(--accent-bg);
          color: var(--accent);
        }

        @media (max-width: 700px) {
          .stats-grid {
            grid-template-columns: 1fr;
          }
        }
      `}</style>
    </>
  );
}
