import React from "react";

type PressItem = { date: string; title: string; summary: string; link: string };

export default function PressNewsCard({
  press,
  onAdd,
  onRemove,
  onChange,
}: {
  press: PressItem[];
  onAdd: () => void;
  onRemove: (index: number) => void;
  onChange: (index: number, key: keyof PressItem, value: string) => void;
}) {
  return (
    <>
      {/* Header section on gray background */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginBottom: "14px",
          marginTop: "20px",
        }}
      >
        <h3 style={{ fontSize: "16px", fontWeight: 600 }}>Press News</h3>
        <button
          onClick={onAdd}
          className="primary-btn"
          style={{
            background: "var(--accent)",
            color: "#072c21",
            fontWeight: 500,
            borderRadius: "6px",
            padding: "6px 12px",
          }}
        >
          + Add Press Item
        </button>
      </div>

      {/* Each press item is now a floating card */}
      <div className="press-list">
        {press.map((p, i) => (
          <div key={i} className="press-card">
            <div className="row">
              <input
                placeholder="Title"
                value={p.title}
                onChange={(e) => onChange(i, "title", e.target.value)}
              />
              <input
                type="date"
                style={{ width: "140px" }}
                value={p.date}
                onChange={(e) => onChange(i, "date", e.target.value)}
              />
              <button className="trash-btn" onClick={() => onRemove(i)}>
                
              </button>
            </div>

            <textarea
              placeholder="Description"
              value={p.summary}
              onChange={(e) => onChange(i, "summary", e.target.value)}
            />

            <input
              placeholder="Link (optional)"
              value={p.link}
              onChange={(e) => onChange(i, "link", e.target.value)}
            />
          </div>
        ))}
      </div>

      <style>{`
        .press-list {
          display: flex;
          flex-direction: column;
          gap: 18px;
        }

        .press-card {
          background: var(--panel);
          border: 1px solid var(--border);
          border-radius: 10px;
          padding: 16px;
          box-shadow: 0 4px 10px rgba(0,0,0,0.08);
        }

        .press-card .row {
          display: flex;
          gap: 10px;
          align-items: center;
          margin-bottom: 10px;
        }

        .press-card input,
        .press-card textarea {
          background: var(--panel);
          border: 1px solid var(--border);
          border-radius: 6px;
          color: var(--text);
          font-size: 13px;
          padding: 8px 10px;
          width: 100%;
        }

        .press-card textarea {
          resize: vertical;
          min-height: 60px;
          margin-bottom: 8px;
        }

        .trash-btn {
          border: 1px solid var(--border);
          border-radius: 6px;
          padding: 6px 10px;
          height: 34px;
          cursor: pointer;
          background: var(--panel);
        }
        .trash-btn:hover {
          background: var(--accent-bg);
          color: var(--accent);
        }
      `}</style>
    </>
  );
}
