export function useStaticConfig(defaults: any = {}) {
  if (typeof window === "undefined") {
    return defaults;
  }

  const [config, setConfig] = useState(defaults);

  useEffect(() => {
    const cfg = (window as any).MAIN_CONTENT;
    if (cfg && typeof cfg === "object") {
      setConfig({ ...defaults, ...cfg });
    }
  }, []);

  return config;
}
