import React from "react";

export default function HonoreePreviewCard({ data }: { data: any }) {
  // handle both nested (`form`) and flat formats
  const form = data?.form || data || {};

  const name = form.name || "Unnamed Honoree";
  const type = form.type || "Individual";
  const category = form.category || "Cultural Elites";
  const field = form.field || "";
  const location = form.location || "";
  const image = form.image || "";
  const certificate = data?.certificates?.[0]?.code || "";
  const stats = Array.isArray(data?.stats)
    ? data.stats.map((s: any) =>
        s.label && s.value ? `${s.label}: ${s.value}` : s.label || s.value
      )
    : [];
  const achievements = Array.isArray(data?.achievements)
    ? data.achievements.map((a: any) => a.title || a.description)
    : [];
  const press = Array.isArray(data?.press)
    ? data.press.map((p: any) => p.title)
    : [];
  const keywords = data?.keywords || [];

  return (
    <div className="honoree-preview-card">
      <style>{`
        :root {
          --deep-navy: #1A202C;
          --prestige-gold: #D1A12A;
          --signature-teal: #00838F;
          --off-white: #F7FAFC;
          --neutral-gray: #A0AEC0;
        }

        .honoree-preview-card {
          background: var(--deep-navy);
          border-radius: 16px;
          overflow: hidden;
          color: var(--off-white);
          box-shadow: 0 10px 20px rgba(0,0,0,0.25);
          max-width: 600px;
          margin: 0 auto;
          transition: transform .3s ease;
        }

        .honoree-image {
          width: 100%;
          height: 320px;
          object-fit: cover;
        }

        .honoree-body {
          padding: 24px 28px;
          display: flex;
          flex-direction: column;
          gap: 8px;
        }

        .honoree-name {
          font-family: "Montserrat", sans-serif;
          font-size: 1.8rem;
          font-weight: 700;
          color: var(--prestige-gold);
        }

        .honoree-meta {
          font-size: 0.95rem;
          color: var(--signature-teal);
        }

        .honoree-field {
          font-size: 1rem;
          font-weight: 500;
          color: var(--off-white);
        }

        .honoree-location {
          font-size: 0.9rem;
          color: var(--neutral-gray);
        }

        .honoree-certificate {
          margin-top: 8px;
          font-size: 0.9rem;
          color: var(--prestige-gold);
        }

        .honoree-stats,
        .honoree-achievements,
        .honoree-press,
        .honoree-keywords {
          margin-top: 14px;
          font-size: 0.9rem;
          color: var(--off-white);
        }

        .honoree-achievements ul,
        .honoree-press ul,
        .honoree-keywords ul {
          list-style: disc;
          margin: 6px 0 0 20px;
        }
      `}</style>

      {image && <img src={image} alt={name} className="honoree-image" />}

      <div className="honoree-body">
        <div className="honoree-name">{name}</div>
        <div className="honoree-meta">
          {type}  {category}
        </div>
        {field && <div className="honoree-field">{field}</div>}
        {location && <div className="honoree-location">{location}</div>}
        {certificate && (
          <div className="honoree-certificate">
            Certificate: {certificate}
          </div>
        )}

        {stats.length > 0 && (
          <div className="honoree-stats">
            <strong>Highlights:</strong>
            <ul>
              {stats.map((s, i) => (
                <li key={i}>{s}</li>
              ))}
            </ul>
          </div>
        )}

        {achievements.length > 0 && (
          <div className="honoree-achievements">
            <strong>Achievements:</strong>
            <ul>
              {achievements.map((a, i) => (
                <li key={i}>{a}</li>
              ))}
            </ul>
          </div>
        )}

        {press.length > 0 && (
          <div className="honoree-press">
            <strong>Press:</strong>
            <ul>
              {press.map((p, i) => (
                <li key={i}>{p}</li>
              ))}
            </ul>
          </div>
        )}

        {keywords.length > 0 && (
          <div className="honoree-keywords">
            <strong>Keywords:</strong>
            <ul>
              {keywords.map((kw, i) => (
                <li key={i}>#{kw}</li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </div>
  );
}
