// Utility functions for slug generation and honoree lookup
import { honoreesData, Honoree } from "./data";

export const generateSlug = (name: string): string => {
  return name
    .toLowerCase()
    .replace(/[^a-z0-9\s-]/g, '')
    .replace(/\s+/g, '-')
    .replace(/-+/g, '-')
    .trim();
};

export const findHonoreeBySlug = (slug: string): Honoree | undefined => {
  return honoreesData.find(honoree => generateSlug(honoree.name) === slug);
};

export const getAllHonoreeSlugs = (): string[] => {
  return honoreesData.map(honoree => generateSlug(honoree.name));
};